	import uniPopup from '@/components/uni-popup/uni-popup.vue'
	import uniPopupMessage from '@/components/uni-popup/uni-popup-message.vue'
export default {
	data(){
		return {
			msg:"",
			index:0,
			classname:"",
			array:[],
			page:1,
			pageSize:10,
			leaveArray:[],
			hasMoreData:true,
		}
	},
	  onLoad() {
	 	this.initClassList();
	  },
	  
	methods:{
		//跳转请假记录
		openRecord(){
			uni.navigateTo({
				url:'../askLeave/askLeave'
			})
		},
		bindPickerChange(e){
			this.index=e.target.value;
			this.classname=this.array[this.index].CLASSNAME;
		},
		//获取用户下面的班级列表
		initClassList(){
			var that=this;
			var app=getApp().globalData
			uni.request({
				url:app.url+"public/getClassListWithTeacher",
				method:"post",
				header: {
				        //设置参数内容类型为json
				       'Content-Type': 'application/x-www-form-urlencoded', // 默认值
						 Authorization:app.token// 默认值
				      },
				dataType: 'json',
				success:(res)=>{
					if(res.data=="reLogin"){
						uni.showModal({
							title:"提示",
							content:"登录过期，请重新登录",
							showCancel:false,
							success:function(res) {
								if(res.confirm){
									uni.reLaunch({
										url:"../login/login"
									})
								}
							}
						})
					}else{
						if(res.data.result==true){
							that.array=res.data.data;
							app.globalclassID=res.data.data[that.index].CLASSID;
							that.initLeave();
						}else{
							that.array=[];
						}
					}
				},
				fail:(res)=>{
					that.msg="服务器异常!";
					this.$refs.oftenProblem.open();
				}
			})
		},
		//获取请假列表
		initLeave(){
			uni.showLoading({
				title:"加载中"
			})
			var that=this;
			var app=getApp().globalData;
			uni.request({
				url:app.url+"askLeave/getLeaveList",
				method:"post",
				header: {
				        //设置参数内容类型为json
				        'Content-Type': 'application/x-www-form-urlencoded',
				        Authorization:app.token
				      },
				dataType:"json",
				data:{
						classID:app.globalclassID,
						status:"0",
						curpage:that.page,
						pagesize:that.pageSize
				},
				success:(res)=>{
					var arr=res.data;
					uni.hideLoading();
					if(arr=="reLogin"){
						uni.showModal({
							title:"提示",
							content:"登录过期，请重新登录",
							showCancel:false,
							success:function(res) {
								if(res.confirm){
									uni.reLaunch({
										url:"../login/login"
									})
								}
							}
						})
					}else{
						if(arr.result==true){
							for(var item of arr.data){
								item.IMAGE="data:image/png;base64,"+item.IMAGE.replace(/[\r\n]/g,"");
							}
							if(arr.data!=undefined){
								if(that.page==1){
									that.leaveArray=arr.data;
								}else{
									that.leaveArray=that.leaveArray.concat(arr.data)
								}
								if(that.leaveArray.length>=arr.count){
									that.hasMoreData=false;
								}else{
									that.hasMoreData=true;
								}
							}
						}else{
							that.leaveArray=[];
						}
					}
				},
				fail:()=>{
					uni.hideLoading();
					that.msg="服务器异常!";
					this.$refs.oftenProblem.open();
				}
			})
		}
		
	}
}
